.App-header {
  min-width: 100%;
  max-height:300px;
  /* min-height: 100vh; */
  background-color: #1b1d24;
  display: inline-flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(2px + 2vmin);
  color: rgb(230, 230, 230);
}


#title {
  padding-left: 9.25%;
}

#logo{
  width: 200px;
  height: 150px;
  padding-left: 0.5%;
}

#Hamburger-icon{
  width: 150px; 
  height: 150px;
  padding-left: 10%;
}

body{
  background-color: #1f232b;
  
}
.Intro-part{
  display: inline-flex;
}
.Deliver-text{
  font-size: calc(40px + 2vmin);
  color: rgb(207, 141, 19);
  margin-top: 15%;
  margin-left: 8%;
  font-weight: 550;
  font-family:  'Allan';
}

.Deliver-text:hover{
  color:rgb(226, 143, 35);
}

.Price-text{
  font-size: calc(30px + 2vmin);
  color: rgb(214, 211, 211);
  text-align: center;
  padding-top: 4%;
  font-weight: 550;
  font-family:  'Allan';
}
.Price-text:hover{
  color: rgb(230, 230, 230);;
}

.Price-line{
  align-self: center;
  display:flex;
  color: rgb(0, 0, 0);
  justify-content: center;
  font-size: calc(30px + 2vmin);
  text-align: center;
  padding: 1%;
  font-weight: 550;
  font-family:  'Allan';
  background: rgb(196, 133, 16);
  margin-top: 2%;
  margin-left: 35%;
  margin-right: 35%;
  margin-bottom: 50%;
  border-radius: 50px;
}
.Price-line:hover{
  background-color: rgb(226, 143, 35);
}

.Order{
  align-self: center;
  display:flex;
  color: rgb(0, 0, 0);
  justify-content: center;
  font-size: calc(30px + 2vmin);
  text-align: center;
  padding: 1%;
  font-weight: 550;
  font-family:  'Allan';
  background: rgb(196, 133, 16);
  border-radius: 10px;
}
